import Vue from 'vue'
import VueResource from 'vue-resource'
import App from './App'

var root = 'http://104.131.150.75';

// Vue.use(VueTouch)
Vue.use(VueResource)

Vue.http.options.emulateHTTP = true;
Vue.http.options.emulateJSON = true;
Vue.http.options.root = root + '/api/v0.1';

/* eslint-disable no-new */
new Vue({
  el: 'body',
  components: { App }
})



